import app from '../../v1.0/src/app';

export default class app_widget extends app {
    constructor(start_uri, settings, lang) {
        super(start_uri, settings, lang);
    }

    init() {
        this.bindLinks();
        this.renderIcons();
    }

    show(param_element, param_mode) {
        const mode = param_mode || 'forward';
        let uri = param_element && param_element.uri;
        let element = param_element;
        if (mode === 'back') {
            this.history.pop();
            if (!element && this.history && this.history.length) {
                uri = this.history[this.history.length - 1];
                element = this.cache[uri];
            }
        }

        if (
            mode === 'forward' && (
                !this.history.length || this.history[this.history.length - 1] !== element.uri
            )) {
            this.history.push(uri);
        }

        this.popstate({uri, length: this.history.length});
    }
    popstate(param_state) {
        let state = param_state;
        if (!state || !state.uri || !state.length) {
            state = {
                uri: window.location.hash.replace(/^#/, ''),
                length: this.history.length
            };
        }
        const view = this.cache[state.uri];
        const cur_view = this.current_view;
        this.current_view = view;
        if (cur_view && cur_view.view) {
            cur_view.view.classList.remove('in');
        }
        view.view.classList.add('in');
        view.view.classList.remove('hidden');
    }
}
