import Raven from 'raven-js';
import {addEvent, getScrollTop, setScrollTop} from '../../v1.0/src/helpers';
import {configureFlatpickr, configureMustache} from '../../v1.0/src/servicesConfigurators';

import App from './app';
import {GenericView} from '../../v1.0/src/GenericView';
import {
    widget_flight,
    widget_passenger,
    widget_passenger_document,
    widget_seatmap_strategies,
    widget_seatmap_preview,
    error
} from './views';

export class widget_full_checkin extends GenericView {
    constructor(container, options) {
        options = options || {};
        super('checkins/', null, 'widget_full_checkin', options);

        this.auto_serialize = true;
        this.template_url = options.template_url || __CONFIG__.template_url;
        this.template = options.template || 'widget_full_checkin';
        this.onchange = options.onchange;
        this.data.edit_mode = true;
        this.is_new = true;
        this.events = {};
        if (options.success) {
            this.on('success', options.success);
        }
        if (options.error) {
            this.on('error', options.error);
        }
        if (options.ready) {
            this.on('ready', options.ready);
        }
        this.requered_props = {
            flights: [
                'marketing_carrier_code', 'flight_num', 'departure_date',
                'departure_time', 'departure_code', 'destination_code',
            ],
            passengers: [
                'first_name', 'last_name', 'gender'
            ],
            ffns: ['ffn', 'ffn_airline'],
            passports: [
                'passport_number', 'passport_country_of_issue',
                'passport_expiry_date'
            ],
            visas: [
                'visa_number', 'visa_country_of_issue',
                'visa_expiry_date'
            ],
        };
        this.props = {
            flights: this.requered_props.flights.concat([
                'operating_carrier_code', 'booking_ref'
            ]),
            ffns: this.requered_props.ffns,
            passports: this.requered_props.passports.concat(
                ['passport_issue_date']
            ),
            visas: this.requered_props.passports.concat(
                ['visa_issue_date']
            ),
        }
        this.props.passengers = this.requered_props.passengers.concat([
                'middle_name', 'eticket', 'gender', 'citizenship',
                'residence', 'date_of_birth'
            ],
            this.props.ffns,
            this.props.passports,
            this.props.visas
        );

        if (typeof(container) === 'string') {
            this.view = window.document.getElementById(container);
        }
        else {
            this.view = container;
        }
        this.configure();
        if (window.document.getElementById(this.template)) {
            this.init();
        }
        else {
            this.app.ajax({
                url: this.template_url,
                callback: text => {
                    const div = document.createElement('div');
                    div.style.display = 'none';
                    div.innerHTML += text;
                    document.body.appendChild(div);
                    this.init();
                },
                error: (status, text) => {
                    console.error(`get_template error ${status}: ${text}`);
                }
            });
        }
    }

    configure() {
        if (__CONFIG__.sentry.isEnabled) {
            Raven.config(__CONFIG__.sentry.publicDSN, {
                release: __CONFIG__.sentry.release
            }).install();
        }
        const Mustache = configureMustache();
        const flatpickr = configureFlatpickr(__CONFIG__.lang);
        this.app = new App(this.uri,
            Object.assign(__CONFIG__, {
                xhr_timeout: 25000,
                views: this.view,
                token: 'WIDGET',
                token_header: 'X-Token',
                Mustache, flatpickr, Raven,
                classRoutes: [
                    {
                        constructor: widget_full_checkin,
                        className: "widget_full_checkin",
                        pattern: 'checkins/$'
                    },
                    {
                        constructor: widget_passenger,
                        className: "passenger",
                    },
                    {
                        constructor: widget_flight,
                        className: "flight",
                    },
                    {
                        constructor: widget_passenger_document,
                        className: "passenger_document",
                    },
                    {
                        constructor: widget_seatmap_strategies,
                        className: "seatmap_strategies",
                    },
                    {
                        constructor: widget_seatmap_preview,
                        className: "seatmap_preview",
                    },
                    {
                        constructor: error,
                        className: "error",
                        pattern: 'error/'
                    }
               ]
            }),
            {}
        );
        this.app.root = this.view;
    }
    call(event, data) {
        if (that.events[event]) {
            that.events[event].forEach(func => func(data))
        };
    }
    init() {
        const local = {};
        ['och-i18n', 'seatmap_strategies_dict'].forEach((v) => {
            try {
                local[v] = JSON.parse(document.getElementById(v).innerHTML);
            } catch (e) {
                local[v] = {};
            }
        });
        if (local['och-i18n']) {
            this.app.lang = local['och-i18n'];
        }
        if (local['seatmap_strategies_dict']) {
            this.app.settings.seatmap_strategies = local['seatmap_strategies_dict'];
        }
        this.app.subscribe(`checkins/.*`, this);
        this.app.cache[this.uri] = this;
        this.get(() => this.show());
        this.call('ready');
    }

    get(callback) {
        const try_storage = this.app.localStorage('och-checkins-data');
        this.data = this.converter(try_storage || this.data);
        callback();
    }

    dump() {
        const data = {
            email: this.data.data.email,
            seatmap_strategies: this.data.seatmap_strategies.data.data
        };
        const validation = {is_valid: true, flights: [], passengers: []};
        const first_flight =  this.data.data.flights[0] && this.data.data.flights[0].data.data;
        data.flights = this.data.data.flights.map((flight, index) => {
            const data = {};
            this.props.flights.forEach(
                prop => {
                    if (prop in flight.data.data) data[prop] = flight.data.data[prop]
                }
            );
            if (!data.operating_carrier_code) {
                data.operating_carrier_code = data.marketing_carrier_code
            }
            data.booking_ref = first_flight.booking_ref;
            data.num = index + 1;

            return data;
        });
        data.passengers = this.data.data.passengers.map((psg, index) => {
            const data = {};
            this.props.passengers.forEach(
                prop => {
                    if (prop in psg.data.data) data[prop] = psg.data.data[prop]
                }
            );
            widget_passenger_document.documentTypes().forEach(type => {
                if (psg.data.data[type] && psg.data.data[type].length) {
                    const doc = psg.data.data[type][0].data.data;
                    this.props[type].forEach(prop => {
                        if (prop in doc) data[prop] = doc[prop];
                    });
                }
            })
            data.num = index + 1;
            return data;
        });

        ['flights', 'passengers'].forEach(type => {
            data[type].forEach((inst, index) => {
                validation[type][index] = this.requered_props[type].filter(
                    prop => inst[prop] ? false : true
                );
                if (type === 'passengers') {
                    widget_passenger_document.documentTypes().forEach(doc_type => {
                        if (this.requered_props[doc_type].some(prop => inst[prop])) {
                            validation[type][index] = validation[type][index].concat(
                                this.requered_props[doc_type].filter(prop => !inst[prop])
                            );
                        }
                    });
                }
                if (validation[type][index].length) {
                    validation.is_valid = false;
                }
            })
        })
        if (!validation.is_valid) {
            data.validation = validation;
        }
        return data;
    }

    validateForm(validation) {
        const email_control = this.view.querySelector('[name="email"]');
        let is_valid = true;
        if (!email_control.value || !email_control.checkValidity()) {
            is_valid = false;
            this.inputRequired(email_control);
        };
        if (validation && !validation.is_valid) {
            ['flights', 'passengers'].forEach(type => {
                validation[type].forEach((item, index) => {
                    if (item.length) {
                        this.data.data[type][index].validateForm();
                    }
                });
            });
            is_valid = false;
        }
        if (!this.data.data.passengers.length) {
            this.view.querySelectorAll('[data-bind="btn-psg-add"]').forEach(btn => {
                this.inputRequired(btn);
                is_valid = false;
            });
        }
        if (!this.data.data.flights.length) {
            this.view.querySelectorAll('[data-bind="btn-add-flight"]').forEach(btn => {
                this.inputRequired(btn);
                is_valid = false;
            });
        }
        else {
            const has_etickers = this.data.data.passengers.every(
                psg => psg.data.data.eticket
            );
            const first_flight = this.data.data.flights[0].data.data || {};
            if (!first_flight.booking_ref && !has_etickers) {
                is_valid = false;
                ['eticket', 'booking_ref'].forEach(name => {
                    this.view.querySelectorAll(`[name="${name}"]`).forEach(
                        input => {
                            this.inputRequired(input);
                            is_valid = false;
                        }
                    );
                })
            }
        }
        this.view.querySelectorAll('[data-bind="agreement"]').forEach(checkbox => {
            if (!checkbox.checked) {
                this.inputRequired(checkbox);
                is_valid = false;
            }
        });
        if (!is_valid) {
            this.app.scrollTo(this.view.querySelector('.has-required'));
        }
        return is_valid;
    }

    show() {
        this.render();
        this.app.show(this);
        return this;
    }

    converter(data) {
        const classNames = {
            flights: 'flight',
            passengers: 'passenger',
        }
        if (!data.data) {
            data.data = {}
        }
        ['flights', 'passengers'].forEach(key => {
            if (!data.data[key]) {
                data.data[key] = [];
            }
            if (!data.data[key].length) {
                data.data[key].push({});
            }
            data.data[key] = data.data[key].map((data, index) => {
                data.id = index+1;

                const obj = this.app.initObject(
                    `${this.uri}${key}/${data.id}/`,
                    classNames[key],
                    {data},
                    true
                );
                obj.get();
                obj.parent = this;
                if (!data || this.requered_props[key].some(prop => !data[prop])) {
                    obj.is_new = true;
                    obj.data.edit_mode = true;
                }
                return obj;
            });
        });
        this.passengersCountChange(data.data.seatStrategies);
        return data;
    }


    relation_update(relation) {
        if (relation.is_deleted) {
            const collection = this.data.data[relation.className === 'flight'
                ? 'flights'
                : 'passengers'
            ]
            const index = collection.indexOf(relation);
            if (index >-1) {
                if (relation.type === 'widget_passenger') {
                    this.passengersCountChange();
                }
                collection.splice(index, 1);
                this.render();
            }
        }
    }

    passengersCountChange(data) {
        const psg = this.data.data.passengers.length;
        const strategies = this.app.settings.seatmap_strategies[psg];
        this.data.seatmap_strategies = this.app.initObject(
            `seatmap_strategies/${psg}/`, 'seatmap_strategies',
            {
                strategies: strategies,
                data: data || [],
                num: psg
            },
            true
        );
        this.data.seatmap_strategies.get();
    }

    addPassenger(event) {
        const psg = this.app.initObject(
            `${this.uri}passengers/${this.data.data.passengers.length+1}/`,
            'passenger',
            {edit_mode: true, data: {}},
            true
        )
        psg.parent = this;
        psg.is_new = true;
        this.data.data.passengers.push(psg);
        psg.get(false, () => {
            this.passengersCountChange();
            this.render();
        });
    }

    addFlight(event) {
        const flight = this.app.initObject(
            `${this.uri}flights/${this.data.data.flights.length+1}/`,
            'flight',
            {edit_mode: true, data: {}},
            true
        )
        flight.parent = this;
        flight.is_new = true;
        this.data.data.flights.push(flight);
        flight.get(false, () => this.render());
    }

    bindLinks() {
        super.bindLinks();
        addEvent(this.view.querySelectorAll('[data-bind="btn-psg-add"]'), 'click',
            event => this.addPassenger(event)
        );
        addEvent(this.view.querySelectorAll('[data-bind="btn-add-flight"]'), 'click',
            event => this.addFlight(event)
        );
    }

    render() {
        // this.app.localStorage('och-checkins-data', this.dump());
        // this.scrollTop = getScrollTop();
        super.render();
        // setScrollTop(this.scrollTop);
    }

    inputRequired(input) {
        const group = input.closest('[data-bind="form-group"]') || input;
        toggleClass(group, 'has-required', true);
        group.dataset.error = input.dataset.bind === 'agreement'
            ? this.app.lang.requested
            : this.app.lang.need_to_accept;
    }

    formSubmit(form, event) {
        const data = this.dump();
        const that = this;
        if (this.validateForm(data.validation)) {
            this.loading(true);
            this.app.ajax({
                url: `${this.app.settings.endpoints}checkin`,
                method: 'POST',
                data: data,
                callback: (data) => {
                    this.loading(true);
                    that.data.success = data;
                    that.render();
                    this.call('success', data)
                },
                error: (status, data) => {
                    that.data.error = (data && (data.message_human || data.message))
                        || that.app.lang.unknown_error;
                    that.render();
                    this.call('error', {status, data});
                }
            });
        }
    }
}
