import {addEvent, is_empty, toggleClass} from './helpers';
import {GenericComponent, GenericComponentFlipable} from './GenericComponent';

/* -------------------------------------------------------------------------- */

export class passenger_document extends GenericComponent {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'passenger_document'), data);
        this.auto_serialize = true;
        if (!data.data || !data.data.id) {
            this.is_new = true;
        }
        if (!this.data.document_type) {
            [this.data.document_type] = this.uri.replace(this.parent.uri, '')
                .split('/');
        }
        this.document_type = this.data.document_type;
        if (
            this.document_type && !('document_type_locked' in this.data)
        ) {
            this.document_type_locked = true;
        }
    }
    render(level) {
        const doc = this.data.data
        this.data.is_visa = this.document_type === 'visas';
        this.data.is_ffn = this.document_type === 'ffns';
        this.data.is_passport = !this.data.is_visa && !this.data.is_ffn;
        this.data.parent_edit_mode = this.parent.data.edit_mode
            || this.parent.className === 'checkin_passenger';
        this.data.document_type_locked = this.document_type_locked;
        this.data.title = this.app.lang[`document_type_${this.data.document_type}`];
        this.data.country = ['visa', 'passport'].reduce((acc, type) => {
            if (doc[`${type}_issue_country`]) {
                return doc[`${type}_issue_country`].country;
            }
            return acc;
        }, '');
        this.data.num = ['visa', 'passport', 'ffn'].reduce((acc, type) => {
            if (doc[`${type}_num`]) return doc[`${type}_num`];
            return acc;
        }, '');
        this.data.airline = doc.ffn_airline && doc.ffn_airline.name;
        super.render(level);
        this.deleteConfirmation = this.deleteConfirmation
            && this.deleteConfirmation.replace(
                'DOC',
                `${this.data.title} ${this.data.country || this.data.airline} ${this.data.num}`
            );
    }
    bindLinks() {
        const that = this;
        super.bindLinks();
        addEvent(
            this.view.querySelectorAll('[name="document_type"]'), 'change',
            function onChange() {
                that.document_type = that.data.document_type = this.value;
                if (that.document_type === 'passports') {
                    that.data.data.passport_type = this.value;
                }
                that.render();
            }
        );
    }
    delete() {
        const that = this;
        if (this.is_new) {
            this.data.edit_mode = false;
            this.render();
            if (that.events.cancel) {
                that.events.cancel.forEach(func => func())
            };
        } else {
            super.delete(() => {
                const index = that.parent.data.data[that.get_document_type()]
                    .findIndex(el => that === el);
                if (index > -1) {
                    that.parent.data.data[that.get_document_type()].splice(index, 1);
                    that.parent.init();
                    that.parent.render();
                }
            });
        }
    }
    get_document_type() {
        return this.document_type === 'visas'
            ? 'visas'
            : this.document_type === 'ffns'
                ? 'ffns'
                : 'passports';
    }
    serializeDocumentType(data) {
        const new_data = data;
        if (this.get_document_type() === 'passports') {
            new_data.passport_type = data.document_type ||
                this.data.data.document_type ||
                'InternationalPassport';
        }
        if ('document_type' in new_data) delete new_data.document_type;
        return new_data;
    }
    serialize() {
        const data = {};
        const type = this.get_document_type();
        const schema = {
            'passports': [
                'id', 'passport_num', 'passport_issue_country_id',
                'passport_issue_date', 'passport_expiry_date',
                'passport_default', 'document_type', 'passport_type'
            ],
            'visas': [
                'id', 'visa_num', 'visa_issue_country_id', 'visa_valid_from',
                'visa_valid_to'],
            'ffns': ['id', 'ffn_ffn', 'ffn_airline_id']
        }
        schema[type].forEach(key => {
            if (key in this.data.data) {
                data[key] = this.data.data[key];
            }
        });
        return this.serializeDocumentType(data);
    }
    serializeChanges() {
        let data = super.serializeChanges();
        if (!is_empty(data)) {
            data = this.serializeDocumentType(data);
        }
        if (this.data.data.id) data.id = this.data.data.id;
        return data;
    }
    formSubmit(form, event) {
        if (!this.validateForm(form)) {
            const root = form || this.view;
            this.app.scrollTo(root.querySelector('.has-required'));
            return;
        };
        if (this.parent.className === 'passenger') {
            if(this.parent.edit_mode) {
                this.parent.formSubmit(form, event);
                return;
            }
            if (this.is_new && form) {
                form.setAttribute(
                    'action',
                    `${this.parent.uri + this.get_document_type()}/`
                );
            }
        }
        super.formSubmit(form, event);
    }

    static documentTypes() {
        return ['passports', 'visas', 'ffns'];
    }

    static documentTypeIDKey(type) {
        return {
            visas: 'visa_id',
            ffns: 'ffn_id',
            passports: 'passport_id'
        }[type];
    }
    static documentTypeByIDKey(id_key) {
        return {
            visa_id: 'visas',
            ffn_id: 'ffns',
            passport_id: 'passports'
        }[id_key];
    }

}

/* -------------------------------------------------------------------------- */

export class passenger extends GenericComponentFlipable {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'passenger'), data);
        this.init();
    }

    init() {
        this.types = passenger_document.documentTypes();
        if (!this.data || !this.data.data) return;
        this.types.forEach((type) => {
            if (!this.data.data[type] || !this.data.data[type].length) {
                this.data.data[type] = [{}];
            }
            this.data.data[type].forEach((doc, index) => {
                if (doc instanceof passenger_document) {
                    return;
                }
                if(this.data.data.user_request) {
                    const type_one = type.replace(/\w$/, '');
                    const requested_doc_id = this.data.data[`${type_one}_id`];
                    const ur = {};
                    Object.keys(this.data.data.user_request).forEach(key => {
                        if (key.match(type_one)) ur[key] = true;
                    });
                    if (!is_empty(ur)
                        && (!doc.id || doc.id === requested_doc_id )
                    ) {
                        doc.user_request = ur;
                    }
                }
                this.data.data[type].splice(index, 1,
                    this.create_document(type, {"data":doc})
                );
            });
        });
    }

    render(level) {
        this.data.is_male = this.data.data.gender === 'male';
        this.data.is_female = this.data.data.gender === 'female';
        super.render(level);
        this.deleteConfirmation = this.deleteConfirmation
            && this.deleteConfirmation.replace(
                'PSG',
                `${this.data.data.last_name} ${this.data.data.first_name}`
            );
        this.setDocumentViews();
    }

    setDocumentViews() {
        // setting default view for each docuemnt on the right side
        // of card
        const root = this.view.querySelector(
            this.data.edit_mode ? '.content.hidden' : '.content.visible'
        )
        if (!root) return;
        this.types.forEach(type => {
            if (this.data.data[type]) {
                this.data.data[type].forEach(doc => {
                    doc.view = root.querySelector(
                        `.subcomponent[data-uri="${doc.uri}"]`
                    );
                });
            }
        })
    }

    edit(flag) {
        super.edit(flag);
        setTimeout(() => {
            this.render();
        }, 350);
    }

    create_document(type, doc) {
        const url = doc.data.id
            ? `${this.uri}${type}/${doc.data.id}/`
            : this.new_document_url(type);
        if (!doc) {
            doc = {}
        }
        doc.document_type = type
        const obj = this.app.initObject(url, 'passenger_document', doc);
        obj.passenger = this;
        obj.parent = this;
        return obj;
    }

    new_document_url(doc_type) {
        const uri = `${this.uri}${doc_type || ''}`;
        let new_uri;
        let i = 0;
        do {
            new_uri = `${uri}/new${i? `-${i}` : ''}/`;
            i += 1;
        }
        while (this.app.cache[new_uri] && i < 100);
        return new_uri;
    }

    bindLinks() {
        const that = this;
        super.bindLinks();
        addEvent(
            this.view.querySelectorAll('[data-bind="btn-add-document"]'), 'click',
            function onClick(event) {
                event.preventDefault();
                const doc_type = this.dataset.type || 'passports';
                that.data.data[doc_type].push(
                    that.create_document(doc_type, {
                        edit_mode: true,
                        is_new: true,
                        document_type_locked: false,
                        data: {}
                    })
                );
                that.render();
            }
        );
    }

    delete() {
        const that = this;
        const afterDelete = function doAfterDelete() {
            const index = that.parent.data.data
                .findIndex(el => that === el);
            if (index > -1) {
                that.parent.data.data.splice(index, 1);
            }
            that.parent.render();
        };
        if (!this.is_new) {
            super.delete(afterDelete);
        } else {
            afterDelete(this);
            this.destroy();
        }
    }

    submitOnError(form, status, data) {
        super.submitOnError(form, status, data);
        if (
            status === 422 && data && (
                data.message_code == 'ValidationError'
                || data.message_code == 'FormError'
            )
        ) {
            // catch documents validation errors
            this.types.forEach(type => {
                if (data.data[type]) {
                    data.data[type].forEach((doc, index) => {
                        this.data.data[type][index].submitOnError(null, status, {
                            message_code: data.message_code,
                            data: doc
                        })
                    });
                }
            })
        }
    }

    formSubmit(form, event) {
        event.preventDefault();
        if (!this.validateForm(form)) return;
        const data = super.serializeChanges();
        const flags = {};
        // always need both last and first name
        if (data.last_name || data.first_name) {
            if (!data.last_name) data.last_name = this.data.data.last_name;
            if (!data.first_name) data.first_name = this.data.data.first_name;
        }
        this.types.forEach((type) => {
            if (this.data.data && this.data.data[type]) {
                this.data.data[type].forEach((doc) => {
                    const type = doc.get_document_type();
                    if (!data[type]) data[type] = [];
                    data[type].push(doc.serialize());
                    if (doc.changes && doc.changes.length) {
                        flags[type] = true;
                    }
                });
            }
        });
        this.types.forEach((type) => {
            if (!flags[type] && data[type]) {
                delete data[type];
            }
        });

        if (is_empty(data)) {
            console.log("No changes in psg skip POST/PUT");
            this.edit(false);
            this.update();
            return;
        };
        this.loading(true);
        const form_url = this.is_new ?
            this.uri.replace(/new.*$/, '') : this.uri;
        this.app.ajax({
            url: this.app.settings.endpoints + form_url,
            method: this.is_new ? 'POST' : 'PUT',
            data: {data},
            callback: (resp) => {
                this.loading(false);
                this.changes = [];
                this.data = resp;
                if (this.is_new) {
                    const new_uri = this.uri
                        .replace(/new\/\d*\/??$/, `${this.data.data.id}/`);
                    delete this.app.cache[this.uri];
                    this.app.cache[new_uri] = this;
                    this.uri = new_uri;
                    this.view.dataset.uri = new_uri;
                    this.is_new = false;
                }
                this.init();
                this.update();
                this.edit(false);
            },
            error: (status, resp) => {
                this.submitOnError(form, status, resp);
            }
        });
    }
}
