import {addEvent, toggleClass, time} from './helpers';
import {GenericComponent, GenericComponentFlipable} from './GenericComponent';

export class GenericView extends GenericComponent {
    constructor(uri, app, className, data) {
        super(uri, app, className, data);
        if (this.uri.match(/new\/?$/)) {
            this.is_new = true;
            this.data.data = {};
        }
        this.is_view_component = true;
    }

    can_blur() {
        return (
            !this.changes || !this.changes.length
            || this.app.confirm(this.app.lang.unsaved)
        );
    }

    help(force) {
        const that = this;
        if (this.app.localStorage(`${this.className}_help_shown`) && !force) { return; }
        let flag = false;
        this.view.querySelectorAll('[data-help]').forEach((e) => {
            let help = document.createElement('div');
            const placement = 'placement' in e.dataset ? e.dataset.placement : 'bottom';
            help.innerHTML = this.app.Mustache.render(
                document.getElementById('popover').innerHTML,
                {
                    content: e.dataset.help.replace(/\n/, '<br>'),
                    type: placement
                }
            );
            help = help.children[0];

            that.view.appendChild(help);
            help.style.display = 'block';
            help.classList.add('in');

            const rect = e.getBoundingClientRect();
            const rel = that.view.getBoundingClientRect();
            const me = help.getBoundingClientRect();

            if (placement == 'bottom') { help.style.top = `${rect.bottom - rel.top}px`; } else if (placement != 'top') { help.style.top = `${(rect.top + rect.bottom) / 2 - me.height / 2 - rel.top}px`; }
            if (placement == 'top') { help.style.bottom = `${(rel.height - rect.top) + rel.top}px`; }
            if (placement == 'right') { help.style.left = `${rect.right - rel.left}px`; } else if (placement != 'left') { help.style.left = `${(rect.left + rect.right) / 2 - me.width / 2 - rel.left}px`; }
            if (placement == 'left') { help.style.right = `${(rel.width - rect.left) + rel.left}px`; }
            addEvent(help.querySelector('button'), 'click', () => {
                that.view.removeChild(help);
            });
            flag = true;
        });
        if (flag) {
            this.app.localStorage(`${this.className}_help_shown`, true);
        }
    }

    render(level) {
        const template = document.getElementById(this.template);
        if (!template) {
            console.error(`Can't find template ${this.template} when rendering ${this.uri}`);
            return;
        }
        const className = `obj-${this.template}`;
        this.view = document.querySelector(`.${className}[data-uri="${this.uri}"]`);

        if (!this.view) {
            this.view = document.createElement('section');
            this.view.className = `${className} page-wrap hidden`;
        }
        this.view.dataset.uri = this.uri;
        if (this.is_new) {
            this.data.is_new = this.is_new;
        }
        this.view.dataset.title = template.dataset.title ? template.dataset.title : this.view.dataset.title;
        if (this.app.settings.views && this.view.parentElement != this.app.settings.views) {
            this.app.settings.views.appendChild(this.view);
        }

        super.render(level);

        if (this.view.dataset.help) {
            this.help();
        }
        if (!this.shortcuts) { this.shortcuts = []; }
        this.shortcuts.push({
            key: '?',
            event: () => { this.help(true); }
        });
    }

    getOnError(status, data) {
        this.loading(false);
        if (status == 403 || status == 401) {
            this.app.auth(this.app.lang.wrongtoken, this.uri);
        } else if (status === 0) {
            console.error('app is offline');
        } else {
            this.app.error({status, data});
        }
    }

    get(forceUpdate, callback, fail) {
        if (!this.app.token) {
            this.app.auth(this.app.lang.unauthorized, this.uri);
            return;
        }
        if (typeof (callback) !== 'function') {
            callback = () => {};
        }
        if (typeof (this.converter) !== 'function') {
            this.converter = data => data;
        }
        if (this.is_new) {
            if (!this.data) {
                this.data = {};
            }
            this.render();
            callback();
            return;
        }
        const _backend = () => {
            this.app.loading(true);
            this.app.ajax({
                url: this.app.settings.endpoints + this.uri,
                callback: (data) => {
                    this.app.loading(false);
                    if (!this.data) this.data = {};
                    this.data = this.converter(data);
                    this.updated = time();
                    this.app.dataStorage.set(this.uri, data);
                    callback();
                },
                error: (status, data) => {
                    this.getOnError(status, data);
                    if (typeof(fail) === 'function') fail();
                }
            });
        }
        if (!forceUpdate) {
            this.app.dataStorage.get(this.uri, cache => {
                this.data = this.converter(cache);
                this.app.revalidateData(this);
                callback();
            }, _backend);
        }
        else {
            _backend();
        }

        return this;
    }

    destroy() {
        if (this.view && this.view.parentElement == this.app.settings.views) {
            this.app.settings.views.removeChild(this.view);
        }
        let index;
        do {
            index = this.app.history.indexOf(this.uri);
            if (index >= 0) {
                this.app.history.splice(index, 1);
            }
        }
        while (index >= 0);
        super.destroy();
        this.app.popstate(this.app.current_view
            ? this.app.current_view.uri
            : this.app.start_uri
        );
    }

    onHide() {
        if (this.auto_destroy) {
            this.destroy();
        }
        this.app.hideToolTip();
    }

    show(callback) {
        if (!this.view) {
            this.render();
        }
        this.app.show(this);
        if (typeof (callback) === 'function') {
            callback();
        }
        return this;
    }
}

export class GenericViewFlipable extends GenericView {
    constructor(uri, app, className, data) {
        super(uri, app, className, data);
    }
    edit(status) {
        GenericComponentFlipable.prototype.edit.call(this, status);
    }
}

export class GenericViewOffline extends GenericView {
    constructor(uri, app, className, data) {
        super(uri, app, className, data);
        this.auto_destroy = true;
    }
    show(callback) {
        this.render();
        this.app.show(this);
    }
    get(forceUpdate, callback, fail) {
        if (typeof (callback) === 'function') {
            callback();
        }
        return this;
    }
}


