import {addEvent} from '../../v1.0/src/helpers';
import {flight} from '../../v1.0/src/viewFlight';
import {passenger, passenger_document} from '../../v1.0/src/viewPassenger';
import {seatmap_strategies } from '../../v1.0/src/viewSeatmapStrategies'
import {seatmap_preview} from '../../v1.0/src/viewSeatmapStrategies'
import {GenericComponent} from '../../v1.0/src/GenericComponent'

function save(that) {
    addEvent(that.view.querySelectorAll(
        '[data-bind="save"],[type="submit"]'),
        'click',
        () => {
            if (!that.validateForm()) {
                that.app.scrollTo(that.view.querySelector('.has-required'));
                return;
            }
            that.is_new = false;
            that.data.edit_mode = false;
            that.update();
            that.render();
        }
    );
}

function edit(that, flag) {
    that.data.edit_mode = flag;
    that.render();
}

function destroy(that) {
    const message = that.deleteConfirmation
        || that.app.lang.deleteConfirmation;
    if (!that.app.confirm(message)) return;
    that.destroy();
}


export class widget_flight extends flight {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'widget_flight'), data);
        this.template = 'flight';
        this.auto_serialize = true;
        this.data.first = (data.data.id == 1);
    }
    initForm() {
        save(this);
        super.initForm();
    }
    delete() {destroy(this)}
    // edit(flag) {edit(this, flag)}
}

