export class widget_seatmap_preview extends seatmap_preview {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'widget_seatmap_preview'), data);
        this.template = 'seatmap_preview';
    }
}

export class widget_seatmap_strategies extends seatmap_strategies {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'widget_seatmap_strategies'), data);
    }

    toggleModal(flag) {
        this.view.querySelectorAll('[data-bind="seatmap-container-backdrop"], [data-bind="seatmap-container"]')
            .forEach(el => {
                if (flag) el.classList.add('in')
                    else el.classList.remove('in');
            });
        if (flag) {
            window.document.body.classList.add('modal-open');
        }
        else {
            window.document.body.classList.remove('modal-open');
        }
    }

    initForm() {
        super.initForm();
        addEvent(this.view.querySelectorAll('[data-bind="show-widget"]'), 'click',
            event => {
                event.preventDefault();
                const parent = event.target.closest('[data-bind="drag"]');
                const strategy = this.data.strategies.find(
                    dic => dic.id === parent.querySelector('[name="strategy"]').value
                );
                const seatmap_preview = this.seatmap_preview();
                const modifiers = seatmap_strategies.serializeModifiers(
                    parent.querySelector('[data-bind="plane-selector"]')
                );
                if (strategy.has_modifiers && modifiers) {
                    this.applyStrategy(seatmap_preview, [{
                        name: strategy.id, modifiers
                    }], this.data.num);
                }
                else {
                    this.applyStrategy(seatmap_preview,[strategy.id], this.data.num);
                }
                this.toggleModal(true);
            }
        );
        addEvent(this.view.querySelectorAll('[data-dismiss="modal"]'), 'click',
            () => this.toggleModal(false)
        )
    }
}

export class error extends GenericComponent {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'error'), data);
        this.auto_destroy = true;

        if (!data.message && data.data) {
            if (data.data.message) {
                this.data.message = this.data.data.message;
                this.data.message_human = this.data.data.message_human;
                this.data.title = '';
            } else {
                this.data.html = this.data.data;
            }
        }

        if (data && !data.expected && window.Raven) {
            Raven.captureException(new Error(`${this.data.title}\n${this.data.message}`));
        }
    }
    toggle(state, callback) {
        this.view.querySelectorAll('.fade').forEach(fade => {
            toggleClass(fade, 'in', state);
        });
        setTimeout(callback, 500);
    }
    bindLinks() {
        super.bindLinks();
        this.toggle(true);
        addEvent(this.view.querySelectorAll('[data-dismiss="modal"]'), 'click', () => {
            this.toggle(false, () => {
                this.destroy();
            });
        })
    }
    destroy() {
        this.view.parentElement.removeChild(this.view);
        delete this.app.cache[this.uri];
    }
}