export default class platform {
    constructor(app) {
        this.name = 'web';
        this.app = app;
        this.settings = {
            COOKIE_NAME: 'cin_sid',
            LANDING_PAGE: 'https://1check.in',
        }
    }
    logout() {
        window.document.cookie =  `${this.settings.COOKIE_NAME}=; expires=Thu, 01 Jan 1970 00:00:01 GMT;`;
        try {
            window.localStorage.removeItem('token');
            this.app.settings.token = '';
        } catch(err) {};
        if (window.location.pathname.match(/\w{32}\/$/)) {
            window.location.replace(window.location.pathname
                .replace(/\w{32}\/$/, ''));
        }
        else {
            this.app.get('login/');
        }
    }
    onReceivePush(payload) {
        this.app.get(payload.url, null, null, null, true);
    }
    showBoarding(id, url) {
        return true;
    }
}
